import { useMemo } from "react"
import { useUser } from "@clerk/clerk-react"
import { FilesCustomerIO } from "@rp/common/utils"
import { mapFilesToCustomerIO } from "../utils/mapFilesToCustomerIO"
import { useMultipleFiles } from "./useMultipleFiles"

type UseFilesToCustomerIOProps = Array<string>

type UseFilesToCustomerIOState = {
  files: Array<FilesCustomerIO>
}

const useFilesToCustomerIO = (filesIds: UseFilesToCustomerIOProps): UseFilesToCustomerIOState => {
  const { user } = useUser()

  const allSelectedFiles = useMultipleFiles(filesIds)

  const files = useMemo(
    () => allSelectedFiles.map(({ data }) => mapFilesToCustomerIO(data, user)),
    [allSelectedFiles, user],
  )

  return { files }
}

export { useFilesToCustomerIO }
export type { UseFilesToCustomerIOProps, UseFilesToCustomerIOState }
